export function getCodeForOauth() {  //{code: xxx}
	return new Promise((resolve, reject) => {
		getProvider('oauth').then(proRes => {
			// console.log(proRes) //weixin
			getLoginCode(proRes).then(codeRes => {
				resolve(codeRes)
			}, err => {
				reject(err)
			})
			})
		})
}

export function getProvider(service) {
	return new Promise((resolve, reject) => {
		uni.getProvider({
			service,
			success: (res) => {

				resolve(res.provider[0])
				// //
				// if (service === 'oauth') {
				// 	getLoginCode(res.provider[0]).then(res => {
				// 		resolve(res)
				// 	}, err => {
				// 		reject(err)
				// 	})
				// }

			}

		})
	})
}
//code
function getLoginCode(provider) {
	return new Promise((resolve, reject) => {
		uni.login({
			provider,
			success(res) {
				resolve(res)
			},
			fail(err) {
				uni.showToast({
					title: ''
				});
				reject(err)
			}
		})
	})
}
