import {proxyLaunchHook,callwaitHooks} from './hooks'

/**
 *  app.vue
 * @param {Object} Router 	Router
 * 
 * this  page 
 */
export const callAppvueHooks=async function(Router){
	await callwaitHooks();
}
/**
 * app
 * 
 * @param {Object} Router 	Router
 * 
 * this  page 
 */
export const appletsInit=function(Router){
	proxyLaunchHook.call(this)
}